import React from "react";
import Card from './Card'
import './CardList.css'

const CardList = (props) => {
    return (
        <div className="background">
            {props.robots.map((user, i) => {
                return <Card 
                    key={i}
                    name={user.name}
                    email={user.email}
                    id={user.id}></Card>
            })}
        </div>
    )
}

export default CardList;
