import React from "react";
import './Card.css'
const Card = ({name,email,id}) =>{
    return(
        <div className="bg-light-green1">
            <img src={`https://robohash.org/${id}?200x200`} alt="pankaj" />
            <div> 
                <h3>{name}</h3>
                <p>{email}</p>
            </div>
        </div>
    );
}

export default Card;